/* harmony default export */ var Car = ({
  name: 'car',
  components: {
    myHeader: HeaderBack["a" /* default */],
    myGoodsItem: components_Car_GoodsItem
  },
  data: function data() {
    return {
      header: {
        HBL_icon: "icon_back",
        HBC_text: "购物车",
        HBR_icon: "icon_search",
        HBR_type: "search",
        HBR_show: true
      },
      selectedAll: false,
      payGoodsNum: 0,
      payMoney: 0,
      goodsItems: [],
      //购物车是否是空的 
      isNoEmpty: true
    };
  },
  created: function created() {
    console.log('car===================created');
  },
  mounted: function mounted() {
    console.log('car===================mounted');
  },
  activated: function activated() {
    var carArr = this.$store.state.car;
    if (carArr.length === 0) {
      this.isNoEmpty = false;
      return;
    }
    this.goodsItems = carArr;
    console.log('car===================activated');
  },
  deactivated: function deactivated() {
    console.log('car===================deactivated');
  },
  destroyed: function destroyed() {
    console.log('car===================destroyed');
  },

  methods: {
    goToPay: function goToPay() {
      if (this.getPayGoods() == false) {
        this.$vux.toast.show({
          type: 'warn',
          text: '请选择商品...'
        });
        return;
      };
      this.$router.push({ path: '/payMent' });
    },
    goTORecommend: function goTORecommend() {
      this.$router.push({ path: '/moreHot/recommend' });
    },
    selectedAllFn: function selectedAllFn() {
      var selected = this.selectedAll;
      this.selectedAll = !selected;
      this.goodsItems.forEach(function (item, index) {
        item.selected = !selected;
      });
    },

    // 获取结算的商品
    getPayGoods: function getPayGoods() {
      var arr = [];
      this.goodsItems.forEach(function (ele, i) {
        if (ele.selected) {
          arr.push(ele);
        }
      });
      this.$store.commit("setData", {
        key: 'payGoods',
        value: arr
      });
      return arr;
    },
    carEmpty: function carEmpty(data) {
      this.isNoEmpty = false;
    }
  },
  watch: {
    goodsItems: {
      handler: function handler(curVal, oldVal) {
        var self = this,
            payGoodsNum = 0,
            payMoney = 0;
        self.selectedAll = true;
        curVal.forEach(function (item, index) {
          if (!item.selected) {
            self.selectedAll = false;
          } else {
            payGoodsNum += item.proNum;
            payMoney += item.proNum * item.price;
          }
        });
        self.payGoodsNum = payGoodsNum;
        self.payMoney = payMoney.toFixed(2);
      },
      deep: true
    }
  }

});
